	module.exports = {"data":{"markdownRemark":{"html":"<h3>The 90 Day Leaderboard</h3>\n<p>The new rolling 90 day leaderboard ranks hackers based on their score from this calculation: <strong>Reputation x Signal Percentile x Impact Percentile</strong>. </p>\n<h3>Needs More Info</h3>\n<p>When a program member adds a comment to an open report with a question mark, Hackbot will prompt them asking if they want to change the state of the report to Needs more info. </p>\n<h3>Response Efficiency Timers</h3>\n<p>Response efficiency timers no longer trigger for reports submitted by internal members of the program. </p>\n<h3>Auto-Invites for Controlled Programs</h3>\n<p>Programs in controlled launch mode are no longer able to toggle auto-invites as on or off. To change their settings for invitations, they can contact HackerOne support. </p>\n<h3>Bug Fixes</h3>\n<ul>\n<li>URLs in the report title are now wrapped so that they aren’t crossing out of the inbox. </li>\n<li>The Program Health Dashboard now displays 0 instead of N/A when there are no missed or failed reports. </li>\n<li>When a hacker leaves a program that they got invited to through the email forwarding feature, they won’t be placed in the priority queue for leaving that program. This prevents hackers from harvesting a ton of private invitations. </li>\n<li>Hackers now don’t receive invitations to programs they’ve left. </li>\n<li>When hackers received an invitation to claim a report, they couldn’t see or accept the terms of the program. Now they can actually claim the report and see the terms of the program. </li>\n</ul>","frontmatter":{"path":"/changelog/2018/april","title":"April 2018"}}},"pathContext":{}}

